.header-acide {
  width: 306px;
  height: 100vh;
  background: $default;

  &__box {
    width: 242px;
    padding: 36px 28px;
  }

  &__logo {
    display: flex;
    align-items: center;
    cursor: pointer;

    &--text {
      display: flex;
      align-items: end;

      h2 {
        font-size: 26px;
        line-height: 30px;
        font-weight: var(--font-sb);
        color: $c101;
        margin: 0 4px 0 8px;
      }

      p {
        font-size: 10px;
        line-height: 18px;
        font-weight: var(--font-m);
      }
    }
  }

  &__list {
    margin-top: 54px;

    ul {
      li {
        width: 250px;
        button {
          display: flex;
          align-items: center;
          height: 46px;
          width: 100%;
          background: transparent;
          border-radius: 8px;
          padding-left: 11px;
          p {
            color: $c102;
            font-weight: var(--font-m);
            text-align: left;
            padding-left: 10px;
          }

          &.active {
            background: $c109;

            p {
              color: $default;
            }

            svg {
              path {
                stroke: #fff;
              }
            }

          }

          svg {
            margin-right: 14px;
          }
        }
      }

      li + li {
        margin-top: 10px;
      }
    }
  }

  &__btn {
    &--arrow {
      position: relative;

      &.active:before {
        background-image: url('/img/svg/acid-chevrone-1-white.svg');
      }
    }

    &--arrow::before {
      position: absolute;
      content: '';
      background-image: url('/img/svg/acid-chevrone-1.svg');
      background-repeat: no-repeat;
      background-position: center;
      -webkit-background-size: contain;
      background-size: contain;
      width: 16px;
      height: 16px;
      top: 35%;
      right: 5%;

    }

  }


}